import { updateObject } from 'utils/updateObject';

export const userCreateStart = (state, action) => {
  return updateObject(state, {
    create: {
      ...state.create,
      error: false,
      errorMessage: null,
      loading: false,
      success: false,
      successMessage: null,
      user: null
    }
  });
};

export const userCreateSuccess = (state, action) => {
  return updateObject(state, {
    create: {
      ...state.create,
      error: false,
      errorMessage: null,
      loading: false,
      success: true,
      successMessage: action.message,
      user: action.user
    }
  });
};

export const userCreateFail = (state, action) => {
  return updateObject(state, {
    create: {
      ...state.create,
      error: true,
      errorMessage: action.message,
      conflicts: action.conflicts,
      loading: false,
      success: false,
      successMessage: null,
      user: null
    }
  });
};
