import { updateObject } from 'utils/updateObject';

export const userSuspendStart = (state, action) => {
  return updateObject(state, {
    suspend: {
      ...state.suspend,
      error: null,
      loading: true
    }
  });
};

export const userSuspendSuccess = (state, action) => {
  return updateObject(state, {
    suspend: {
      ...state.suspend,
      error: null,
      loading: false,
      users: action.users,
      suspended: action.suspended
    }
  });
};

export const userSuspendFail = (state, action) => {
  return updateObject(state, {
    suspend: {
      ...state.suspend,
      error: action.error,
      suspended: null,
      loading: false
    }
  });
};

export const userSuspendClearMessage = (state, action) => {
  return updateObject(state, {
    suspend: {
      ...state.suspend,
      loading: false,
      suspended: null,
      error: false,
      errorMessage: null,
      success: false,
      successMessage: null
    }
  });
};
