import { SESSION_ACTION_TYPES } from 'actions';
import { pathUrls } from 'config';

import {
  adminAuthLoginStart,
  adminAuthLoginSuccess,
  adminAuthLoginFail,
  adminAuthLoginClearMessage,
  adminAuthLogout
} from './auth';

import {
  adminChangePasswordStart,
  adminChangePasswordSuccess,
  adminChangePasswordFail
} from './changePassword';

import {
  adminUserUpdateStart,
  adminUserUpdateSuccess,
  adminUserUpdateFail,
  adminUserClearMessage
} from './update';

const initialState = {
  authentication: {
    loggedIn: false,
    loggedOut: false,
    error: false,
    loading: false,
    success: false,
    errorMessage: null,
    successMessage: null
  },
  user: null,
  update: {
    loading: false,
    error: false,
    errorMessage: null,
    success: null,
    successMessage: null
  },
  changePassword: {
    error: false,
    errorMessage: null,
    loading: false,
    success: false,
    successMessage: null
  },
  resetPassword: {
    phoneNumber: null,
    request: null,
    confirm: null,
    error: false,
    errorMessage: null,
    loading: false,
    success: false,
    successMessage: null
  },
  redirectType: null,
  redirect: {
    loginURL: pathUrls.auth.login,
    dashboardURL: pathUrls.dashboard.default
  }
};

const Reducer = (state = initialState, action) => {
  switch (action.type) {
    case SESSION_ACTION_TYPES.LOGIN.START:
      return adminAuthLoginStart(state, action);
    case SESSION_ACTION_TYPES.LOGIN.SUCCESS:
      return adminAuthLoginSuccess(state, action);
    case SESSION_ACTION_TYPES.LOGIN.FAIL:
      return adminAuthLoginFail(state, action);
    case SESSION_ACTION_TYPES.LOGIN.CLEAR_MESSAGE:
      return adminAuthLoginClearMessage(state, action);

    case SESSION_ACTION_TYPES.LOGOUT:
      return adminAuthLogout(state, action);

    case SESSION_ACTION_TYPES.UPDATE_USER.START:
      return adminUserUpdateStart(state, action);
    case SESSION_ACTION_TYPES.UPDATE_USER.SUCCESS:
      return adminUserUpdateSuccess(state, action);
    case SESSION_ACTION_TYPES.UPDATE_USER.FAIL:
      return adminUserUpdateFail(state, action);
    case SESSION_ACTION_TYPES.UPDATE_USER.CLEAR_MESSAGE:
      return adminUserClearMessage(state, action);

    default:
      return state;
  }
};

export default Reducer;
