import { updateObject } from 'utils/updateObject';

export const userListStart = (state, action) => {
  return updateObject(state, {
    list: {
      ...state.list,
      error: null,
      loading: true
    }
  });
};

export const userListSuccess = (state, action) => {
  return updateObject(state, {
    list: {
      ...state.list,
      error: null,
      loading: false,
      users: action.users
    }
  });
};

export const userListFail = (state, action) => {
  return updateObject(state, {
    list: {
      ...state.list,
      error: action.error,
      loading: false
    }
  });
};
