import { USER_ACTION_TYPES } from 'actions';
import { userCountStart, userCountSuccess, userCountFail } from './countUsers';

import {
  userCreateStart,
  userCreateSuccess,
  userCreateFail
} from './createUser';

import {
  userDeleteStart,
  userDeleteSuccess,
  userDeleteFail,
  userDeleteClearMessage
} from './deleteUsers';

import { usersGetStart, usersGetSuccess, usersGetFail } from './getUsers';

import {
  usersUpdateStart,
  usersUpdateSuccess,
  usersUpdateFail,
  usersUpdateClearMessage
} from './updateUser';

import { userListStart, userListSuccess, userListFail } from './listUsers';

import {
  userSuspendStart,
  userSuspendSuccess,
  userSuspendFail,
  userSuspendClearMessage
} from './suspendUsers';

const initialState = {
  create: {
    error: false,
    errorMessage: null,
    conflicts: null,
    loading: false,
    success: false,
    successMessage: null,
    user: null
  },
  count: {
    error: null,
    loading: false,
    total: null,
    buyers: null,
    sellers: null,
    admins: null
  },
  delete: {
    loading: false,
    deleted: null,
    users: null,
    error: false,
    errorMessage: null,
    success: false,
    successMessage: null
  },
  suspend: {
    loading: false,
    suspended: null,
    users: null,
    error: false,
    errorMessage: null,
    success: false,
    successMessage: null
  },
  get: {
    error: null,
    loading: false,
    users: null
  },
  list: {
    error: null,
    loading: false,
    users: null
  },
  update: {
    loading: false,
    user: null,
    error: false,
    errorMessage: null,
    success: false,
    successMessage: null
  }
};

export const Reducer = (state = initialState, action) => {
  switch (action.type) {
    case USER_ACTION_TYPES.CREATE.START:
      return userCreateStart(state, action);
    case USER_ACTION_TYPES.CREATE.SUCCESS:
      return userCreateSuccess(state, action);
    case USER_ACTION_TYPES.CREATE.FAIL:
      return userCreateFail(state, action);
    case USER_ACTION_TYPES.CREATE.CLEAR_MESSAGE:
      return userCreateStart(state, action);

    case USER_ACTION_TYPES.COUNT.START:
      return userCountStart(state, action);
    case USER_ACTION_TYPES.COUNT.SUCCESS:
      return userCountSuccess(state, action);
    case USER_ACTION_TYPES.COUNT.FAIL:
      return userCountFail(state, action);

    case USER_ACTION_TYPES.LIST.START:
      return userListStart(state, action);
    case USER_ACTION_TYPES.LIST.SUCCESS:
      return userListSuccess(state, action);
    case USER_ACTION_TYPES.LIST.FAIL:
      return userListFail(state, action);

    case USER_ACTION_TYPES.DELETE.START:
      return userDeleteStart(state, action);
    case USER_ACTION_TYPES.DELETE.SUCCESS:
      return userDeleteSuccess(state, action);
    case USER_ACTION_TYPES.DELETE.FAIL:
      return userDeleteFail(state, action);
    case USER_ACTION_TYPES.DELETE.CLEAR_MESSAGE:
      return userDeleteClearMessage(state, action);

    case USER_ACTION_TYPES.GET.START:
      return usersGetStart(state, action);
    case USER_ACTION_TYPES.GET.SUCCESS:
      return usersGetSuccess(state, action);
    case USER_ACTION_TYPES.GET.FAIL:
      return usersGetFail(state, action);

    case USER_ACTION_TYPES.UPDATE.START:
      return usersUpdateStart(state, action);
    case USER_ACTION_TYPES.UPDATE.SUCCESS:
      return usersUpdateSuccess(state, action);
    case USER_ACTION_TYPES.UPDATE.FAIL:
      return usersUpdateFail(state, action);
    case USER_ACTION_TYPES.UPDATE.CLEAR_MESSAGE:
      return usersUpdateClearMessage(state, action);

    case USER_ACTION_TYPES.SUSPEND.START:
      return userSuspendStart(state, action);
    case USER_ACTION_TYPES.SUSPEND.SUCCESS:
      return userSuspendSuccess(state, action);
    case USER_ACTION_TYPES.SUSPEND.FAIL:
      return userSuspendFail(state, action);
    case USER_ACTION_TYPES.SUSPEND.CLEAR_MESSAGE:
      return userSuspendClearMessage(state, action);

    default:
      return state;
  }
};

export default Reducer;
