import { updateObject } from 'utils/updateObject';

export const userCountStart = (state, action) => {
  return updateObject(state, {
    count: {
      ...state.count,
      error: null,
      loading: true
    }
  });
};

export const userCountSuccess = (state, action) => {
  return updateObject(state, {
    count: {
      ...state.count,
      error: null,
      loading: false,
      total: action.total,
      buyers: action.buyers,
      sellers: action.sellers,
      admins: action.admins
    }
  });
};

export const userCountFail = (state, action) => {
  return updateObject(state, {
    count: {
      ...state.count,
      error: action.error,
      loading: false
    }
  });
};
