import { updateObject } from 'utils/updateObject';

export const costingListStart = (state, action) => {
  return updateObject(state, {
    list: {
      ...state.list,
      error: null,
      loading: true
    }
  });
};

export const costingListSuccess = (state, action) => {
  return updateObject(state, {
    list: {
      ...state.list,
      error: null,
      loading: false,
      data: action.data
    }
  });
};

export const costingListFail = (state, action) => {
  return updateObject(state, {
    list: {
      ...state.list,
      error: action.error,
      loading: false
    }
  });
};
