import { updateObject } from 'utils/updateObject';

export const userDeleteStart = (state, action) => {
  return updateObject(state, {
    delete: {
      ...state.delete,
      loading: true,
      error: false,
      errorMessage: null,
      success: false,
      successMessage: null
    }
  });
};

export const userDeleteSuccess = (state, action) => {
  return updateObject(state, {
    delete: {
      ...state.delete,
      loading: false,
      users: action.users,
      deleted: action.deleted,
      error: false,
      errorMessage: null,
      success: true,
      successMessage: action.success
    }
  });
};

export const userDeleteFail = (state, action) => {
  return updateObject(state, {
    delete: {
      ...state.delete,
      deleted: false,
      loading: false,
      error: true,
      errorMessage: action.error,
      success: false,
      successMessage: null
    }
  });
};

export const userDeleteClearMessage = (state, action) => {
  return updateObject(state, {
    delete: {
      ...state.delete,
      deleted: false,
      loading: false,
      error: false,
      errorMessage: null,
      success: false,
      successMessage: null
    }
  });
};
