import { apiUrls, axios } from 'config';
import { USER_ACTION_TYPES } from './actionTypes';

const usersSuspendStart = () => {
  return {
    type: USER_ACTION_TYPES.SUSPEND.START
  };
};

const usersSuspendSuccess = (status, users) => {
  return {
    type: USER_ACTION_TYPES.SUSPEND.SUCCESS,
    users: users,
    suspended: status.suspended
  };
};

const usersSuspendFail = error => {
  return {
    type: USER_ACTION_TYPES.SUSPEND.FAIL,
    error: error
  };
};

const usersSuspendClearMessage = () => {
  return {
    type: USER_ACTION_TYPES.SUSPEND.CLEAR_MESSAGE
  };
};

const usersSuspendClearMessageTimeout = () => {
  return dispatch => {
    setTimeout(() => {
      dispatch(usersSuspendClearMessage());
    }, 3000);
  };
};

export const suspendUsers = (users, suspend) => {
  return (dispatch, getState) => {
    const authHeaders = getState().session.authentication.headers;
    dispatch(usersSuspendStart());
    const options = {
      method: apiUrls.users.suspend.method,
      url: apiUrls.users.suspend.url,
      params: {
        userId: Array.isArray(users) ? users.join(',') : users,
        suspend: suspend
      },
      headers: authHeaders
    };

    axios(options)
      .then(response => {
        dispatch(usersSuspendSuccess(response.data, users));
        dispatch(usersSuspendClearMessageTimeout());
      })
      .catch(err => {
        if (err.response) {
          dispatch(usersSuspendFail(err.response.statusText));
          dispatch(usersSuspendClearMessageTimeout());
        } else {
          dispatch(usersSuspendFail('Internal Server Error'));
          dispatch(usersSuspendClearMessageTimeout());
        }
      });
  };
};
