import { apiUrls, axios } from 'config';
import { USER_ACTION_TYPES } from './actionTypes';

const usersUpdateStart = () => {
  return {
    type: USER_ACTION_TYPES.UPDATE.START
  };
};

const usersUpdateSuccess = (data, message) => {
  return {
    type: USER_ACTION_TYPES.UPDATE.SUCCESS,
    user: data,
    loading: false,
    success: message
  };
};

const usersUpdateFail = error => {
  return {
    type: USER_ACTION_TYPES.UPDATE.FAIL,
    error: error,
    loading: false
  };
};

const usersUpdateClearMessage = () => {
  return {
    type: USER_ACTION_TYPES.UPDATE.CLEAR_MESSAGE
  };
};

const usersUpdateClearMessageTimeout = () => {
  return dispatch => {
    setTimeout(() => {
      dispatch(usersUpdateClearMessage());
    }, 3000);
  };
};

export const updateUserDetail = user => {
  return (dispatch, getState) => {
    const authHeaders = getState().session.authentication.headers;
    dispatch(usersUpdateStart());
    const options = {
      method: apiUrls.users.update.method,
      url: apiUrls.users.update.url,
      data: {
        ...user
      },
      headers: authHeaders
    };
    axios(options)
      .then(response => {
        dispatch(
          usersUpdateSuccess(response.data, 'User Successfully Updated')
        );
        dispatch(usersUpdateClearMessageTimeout());
      })
      .catch(err => {
        dispatch(usersUpdateFail(err.response.statusText));
        dispatch(usersUpdateClearMessageTimeout());
      });
  };
};
