import { updateObject } from 'utils/updateObject';

export const usersGetStart = (state, action) => {
  return updateObject(state, {
    get: {
      ...state.get,
      error: null,
      loading: true,
      users: null
    }
  });
};

export const usersGetSuccess = (state, action) => {
  return updateObject(state, {
    get: {
      id: state.get.id + 1,
      ...state.get,
      error: null,
      loading: false,
      users: action.users
    }
  });
};

export const usersGetFail = (state, action) => {
  return updateObject(state, {
    get: {
      ...state.get,
      error: action.error,
      loading: false,
      users: null
    }
  });
};
