import { updateObject } from 'utils/updateObject';

export const adminUserUpdateStart = (state, action) => {
  return updateObject(state, {
    update: {
      error: null,
      loading: true,
      success: null
    }
  });
};

export const adminUserUpdateSuccess = (state, action) => {
  return updateObject(state, {
    update: {
      error: null,
      loading: false,
      success: true,
      successMessage: action.message
    },
    user: {
      ...state.update
    }
  });
};

export const adminUserUpdateFail = (state, action) => {
  return updateObject(state, {
    update: {
      error: true,
      loading: false,
      success: false,
      errorMessage: action.message
    }
  });
};

export const adminUserClearMessage = (state, action) => {
  return updateObject(state, {
    update: {
      error: false,
      loading: false,
      success: false,
      errorMessage: null,
      successMessage: null
    }
  });
};
