import { apiUrls, axios } from 'config';
import { USER_ACTION_TYPES } from './actionTypes';

const usersDeleteStart = () => {
  return {
    type: USER_ACTION_TYPES.DELETE.START
  };
};

const usersDeleteSuccess = (status, users, message) => {
  return {
    type: USER_ACTION_TYPES.DELETE.SUCCESS,
    users: users,
    deleted: status.deleted,
    success: message
  };
};

const usersDeleteFail = error => {
  return {
    type: USER_ACTION_TYPES.DELETE.FAIL,
    error: error
  };
};
const usersDeleteClearMessage = () => {
  return {
    type: USER_ACTION_TYPES.DELETE.CLEAR_MESSAGE
  };
};

const usersDeleteClearMessageTimeout = () => {
  return dispatch => {
    setTimeout(() => {
      dispatch(usersDeleteClearMessage());
    }, 3000);
  };
};

export const deleteUsers = users => {
  return (dispatch, getState) => {
    const authHeaders = getState().session.authentication.headers;
    dispatch(usersDeleteStart());
    const options = {
      method: apiUrls.users.delete.method,
      url: apiUrls.users.delete.url,
      params: {
        userId: Array.isArray(users) ? users.join(',') : users
      },
      headers: authHeaders
    };

    axios(options)
      .then(response => {
        dispatch(
          usersDeleteSuccess(response.data, users, 'Users Deleted Successfully')
        );
        dispatch(usersDeleteClearMessageTimeout());
      })
      .catch(err => {
        if (err.response) {
          dispatch(usersDeleteFail(err.response.statusText));
          dispatch(usersDeleteClearMessageTimeout());
        } else {
          dispatch(usersDeleteFail('Internal Server Error'));
          dispatch(usersDeleteClearMessageTimeout());
        }
      });
  };
};
