import { COSTING_ACTION_TYPES } from 'actions';

import {
  costingListStart,
  costingListSuccess,
  costingListFail
} from './listCosting';



const initialState = {
  list: {
    error: null,
    loading: false,
    data: null
  },
};

export const Reducer = (state = initialState, action) => {
  switch (action.type) {

    case COSTING_ACTION_TYPES.LIST.START:
      return costingListStart(state, action);
    case COSTING_ACTION_TYPES.LIST.SUCCESS:
      return costingListSuccess(state, action);
    case COSTING_ACTION_TYPES.LIST.FAIL:
      return costingListFail(state, action);

    default:
      return state;
  }
};

export default Reducer;
