import { axios, apiUrls } from 'config';
import { USER_ACTION_TYPES } from './actionTypes';

export const usersCountStart = () => {
  return {
    type: USER_ACTION_TYPES.COUNT.START
  };
};

export const usersCountSuccess = data => {
  return {
    type: USER_ACTION_TYPES.COUNT.SUCCESS,
    total: data.total,
    buyers: data.buyers,
    sellers: data.sellers,
    admins: data.admins
  };
};

export const usersCountFail = error => {
  return {
    type: USER_ACTION_TYPES.COUNT.FAIL,
    error: error
  };
};

const usersCountClearMessage = () => {
  return {
    type: USER_ACTION_TYPES.COUNT.CLEAR_MESSAGE
  };
};

const usersCountClearMessageTimeout = () => {
  return dispatch => {
    setTimeout(() => {
      dispatch(usersCountClearMessage());
    }, 3000);
  };
};

export const getUserCount = () => {
  return (dispatch, getState) => {
    const authHeaders = getState().session.authentication.headers;
    dispatch(usersCountStart());
    const options = {
      method: apiUrls.users.count.method,
      url: apiUrls.users.count.url,
      headers: authHeaders
    };
    axios(options)
      .then(response => {
        dispatch(usersCountSuccess(response.data));
        dispatch(usersCountClearMessageTimeout());
      })
      .catch(err => {
        dispatch(usersCountFail(err.response.statusText));
        dispatch(usersCountClearMessageTimeout());
      });
  };
};
