import { apiUrls, axios } from 'config';
import { USER_ACTION_TYPES } from './actionTypes';

import {
  usersCountStart,
  usersCountSuccess,
  usersCountFail
} from './countUsers';

const usersListStart = () => {
  return {
    type: USER_ACTION_TYPES.LIST.START
  };
};

const usersListSuccess = users => {
  console.log(users)
  return {
    type: USER_ACTION_TYPES.LIST.SUCCESS,
    users: users
  };
};

const usersListFail = error => {
  return {
    type: USER_ACTION_TYPES.LIST.FAIL,
    error: error
  };
};

const usersListClearMessage = () => {
  return {
    type: USER_ACTION_TYPES.LIST.CLEAR_MESSAGE
  };
};

const usersListClearMessageTimeout = () => {
  return dispatch => {
    setTimeout(() => {
      dispatch(usersListClearMessage());
    }, 3000);
  };
};

export const getUserList = (limit, offset) => {
  return (dispatch, getState) => {
    const authHeaders = getState().session.authentication.headers;
    dispatch(usersCountStart());
    dispatch(usersListStart());
    Promise.all([
      axios({
        method: apiUrls.users.count.method,
        url: apiUrls.users.count.url,
        headers: authHeaders
      }),
      axios({
        method: apiUrls.users.list.method,
        url: apiUrls.users.list.url,
        params: {
          limit: limit,
          skip: offset
        },
        headers: authHeaders
      })
    ]).then(([count, list]) => {
      dispatch(usersCountSuccess(count.data));
      dispatch(usersListSuccess(list.data.filter((item)=> {
        if(item.hasOwnProperty('role')){
          return !item.role.includes('Admin');
        }else {
          return true;
        }
      })));
      dispatch(usersListClearMessageTimeout());
    }).catch(([countErr, listErr]) => {
      dispatch(usersCountFail(countErr.response.statusText));
      dispatch(usersListFail(listErr.response.statusText));
      dispatch(usersListClearMessageTimeout());
    });
  };
};
