/* eslint-disable no-unused-vars */
import React, { useState, useRef, useEffect } from 'react'
import { Link as RouterLink } from 'react-router-dom'
import clsx from 'clsx'
import PropTypes from 'prop-types'
import { useDispatch } from 'react-redux'
import { makeStyles, withStyles } from '@material-ui/styles'
import {
  AppBar,
  Button,
  IconButton,
  Toolbar,
  Hidden,
  colors,
  ListItemIcon,
  ListItemText,
  Menu,
  MenuItem
} from '@material-ui/core'
import MenuIcon from '@material-ui/icons/Menu'

import useRouter from 'utils/useRouter'
import {
  AccountCircle,
  ExitToAppSharp,
  Update,
  Visibility,
  Lock
} from '@material-ui/icons'
import { pathUrls } from '../../../../config'
import * as actions from '../../../../actions'

const useStyles = makeStyles(theme => ({
  root: {
    boxShadow: 'none'
  },
  flexGrow: {
    flexGrow: 1
  },
  logoutButton: {
    marginLeft: theme.spacing(1)
  },
  logoutIcon: {
    marginRight: theme.spacing(1)
  },
  profileButton: {
    marginRight: theme.spacing(1)
  },
  profileIcon: {
    marginRight: theme.spacing(1)
  }
}))

const StyledMenu = withStyles({
  paper: {
    border: '1px solid #d3d4d5'
  }
})(props => (
  <Menu
    anchorOrigin={{
      vertical: 'bottom',
      horizontal: 'center'
    }}
    elevation={0}
    getContentAnchorEl={null}
    transformOrigin={{
      vertical: 'top',
      horizontal: 'center'
    }}
    {...props}
  />
))

const StyledMenuItem = withStyles(theme => ({
  root: {
    '&:focus': {
      backgroundColor: theme.palette.primary.main,
      '& .MuiListItemIcon-root, & .MuiListItemText-primary': {
        color: theme.palette.common.white
      }
    }
  }
}))(MenuItem)

const TopBar = props => {
  const { onOpenNavBarMobile, className, ...rest } = props

  const classes = useStyles()
  const { history } = useRouter()
  const dispatch = useDispatch()

  const handleUpdate = () => {
    setAnchorEl(null)
    history.push(pathUrls.profile.updatePath)
  }

  const handleChange = () => {
    setAnchorEl(null)
    history.push(pathUrls.profile.changePasswordPath)
  }

  const [anchorEl, setAnchorEl] = React.useState(null)
  const open = Boolean(anchorEl)

  function handleMenu (event) {
    setAnchorEl(event.currentTarget)
  }

  function handleClose () {
    setAnchorEl(null)
  }

  const handleLogout = () => {
    dispatch(actions.sessionLogout())
  }

  return (
    <AppBar
      {...rest}
      className={clsx(classes.root, className)}
      color="primary"
    >
      <Toolbar variant="dense">
        <RouterLink to="/">
          <img
            alt="Logo"
            src="/images/logos/logo--white.svg"
          />
        </RouterLink>
        <div className={classes.flexGrow} />

        <Hidden mdDown>
          <Button
            className={classes.profileButton}
            color="inherit"
            onClick={handleMenu}
          >
            <AccountCircle className={classes.profileIcon} />
            Profile
          </Button>
          <StyledMenu
            anchorEl={anchorEl}
            id="customized-menu"
            keepMounted
            onClose={handleClose}
            open={Boolean(anchorEl)}
          >
            <StyledMenuItem onClick={handleChange}>
              <ListItemIcon>
                <Lock />
              </ListItemIcon>
              <ListItemText primary="Change Password" />
            </StyledMenuItem>
            <StyledMenuItem onClick={handleUpdate}>
              <ListItemIcon>
                <Update />
              </ListItemIcon>
              <ListItemText primary="Update" />
            </StyledMenuItem>
            <StyledMenuItem onClick={handleLogout}>
              <ListItemIcon>
                <ExitToAppSharp />
              </ListItemIcon>
              <ListItemText primary="Logout" />
            </StyledMenuItem>
          </StyledMenu>
        </Hidden>
        <Hidden lgUp>
          <IconButton
            color="inherit"
            onClick={onOpenNavBarMobile}
          >
            <MenuIcon />
          </IconButton>
        </Hidden>
      </Toolbar>

    </AppBar>
  )
}

TopBar.propTypes = {
  className: PropTypes.string,
  onOpenNavBarMobile: PropTypes.func
}

export default TopBar
