import { apiUrls, axios } from 'config';
import { COSTING_ACTION_TYPES } from './actionTypes';
import { GroupedOrderedCostingList } from './groupedOrderedCostingList';

const costingListStart = () => {
  return {
    type: COSTING_ACTION_TYPES.LIST.START
  };
};

const costingListSuccess = data => {
  return {
    type: COSTING_ACTION_TYPES.LIST.SUCCESS,
    data: data
  };
};

const costingListFail = error => {
  return {
    type: COSTING_ACTION_TYPES.LIST.FAIL,
    error: error
  };
};



export const getCostingList = () => {
  return (dispatch, getState) => {
    const authHeaders = getState().session.authentication.headers;
    dispatch(costingListStart());
    axios({
      method: apiUrls.costing.get.method,
      url: apiUrls.costing.get.url,
      headers: authHeaders
    }).then((response) => {
      dispatch(costingListSuccess(GroupedOrderedCostingList(response.data)))
    }).catch((err) => {
      dispatch(costingListFail(err.response))
    });
  };
};
