import { SESSION_ACTION_TYPES } from './actionTypes';
import { apiUrls, axios, jsonStorage } from 'config';

const sessionUpdateUserStart = () => {
  return {
    type: SESSION_ACTION_TYPES.UPDATE_USER.START
  };
};

const sessionUpdateUserSuccess = (updates, message) => {
  return {
    type: SESSION_ACTION_TYPES.UPDATE_USER.SUCCESS,
    updates: updates,
    message: message
  };
};

const sessionUpdateUserFail = error => {
  return {
    type: SESSION_ACTION_TYPES.UPDATE_USER.FAIL,
    message: error
  };
};

const sessionUpdateClearMessage = () => {
  return {
    type: SESSION_ACTION_TYPES.UPDATE_USER.CLEAR_MESSAGE
  };
};

const sessionUpdateClearMessageTimeout = () => {
  return dispatch => {
    setTimeout(() => {
      dispatch(sessionUpdateClearMessage());
    }, 3000);
  };
};

export const sessionUpdateUserInfo = (firstName, lastName) => {
  return (dispatch, getState) => {
    dispatch(sessionUpdateUserStart());
    const authHeaders = getState().session.authentication.headers;
    const options = {
      method: apiUrls.admin.update.method,
      url: apiUrls.admin.update.url,
      data: {
        firstName: firstName,
        lastName: lastName
      },
      headers: authHeaders
    };
    axios(options)
      .then(response => {
        const update = {
          ...getState().session.auth,
          ...response.data
        };
        jsonStorage.setItem('session', update);
        dispatch(sessionUpdateUserSuccess(update, 'User Updated Successfully'));
        dispatch(sessionUpdateClearMessageTimeout());
      })
      .catch(err => {
        dispatch(sessionUpdateUserFail(err.response.statusText));
        dispatch(sessionUpdateClearMessageTimeout());
      });
  };
};
