import { apiUrls, axios } from 'config';
import { USER_ACTION_TYPES } from './actionTypes';

const usersGetStart = () => {
  return {
    type: USER_ACTION_TYPES.GET.START
  };
};

const usersGetSuccess = users => {
  return {
    type: USER_ACTION_TYPES.GET.SUCCESS,
    users: users,
    error: null,
    loading: null
  };
};

const usersGetFail = error => {
  return {
    type: USER_ACTION_TYPES.GET.FAIL,
    error: error,
    loading: null
  };
};

const usersGetClearMessage = () => {
  return {
    type: USER_ACTION_TYPES.GET.CLEAR_MESSAGE
  };
};

const usersGetClearMessageTimeout = () => {
  return dispatch => {
    setTimeout(() => {
      dispatch(usersGetClearMessage());
    }, 3000);
  };
};

export const getUserDetail = userId => {
  return (dispatch, getState) => {
    const authHeaders = getState().session.authentication.headers;
    dispatch(usersGetStart());
    const options = {
      method: apiUrls.users.detail.method,
      url: apiUrls.users.detail.url,
      params: {
        userId: userId
      },
      headers: authHeaders
    };
    axios(options)
      .then(response => {
        dispatch(usersGetSuccess(response.data));
        dispatch(usersGetClearMessageTimeout());
      })
      .catch(err => {
        dispatch(usersGetFail(err.response.statusText));
        dispatch(usersGetClearMessageTimeout());
      });
  };
};
