import { axios, apiUrls } from 'config';
import { USER_ACTION_TYPES } from './actionTypes';

// CreateRegion Create Actions
const createUserStart = () => {
  return {
    type: USER_ACTION_TYPES.CREATE.START
  };
};

const createUserSuccess = (user, message) => {
  return {
    type: USER_ACTION_TYPES.CREATE.SUCCESS,
    user: user,
    message: message
  };
};

const createUserFail = (error, errors) => {
  return {
    type: USER_ACTION_TYPES.CREATE.FAIL,
    message: error,
    conflicts: errors
  };
};

const createUserClearMessage = () => {
  return {
    type: USER_ACTION_TYPES.CREATE.CLEAR_MESSAGE
  };
};

const createUserClearMessageTimeout = () => {
  return dispatch => {
    setTimeout(() => {
      dispatch(createUserClearMessage());
    }, 3000);
  };
};

export const createUser = date => {
  return (dispatch, getState) => {
    const authHeaders = getState().session.authentication.headers;
    dispatch(createUserStart());
    const options = {
      method: apiUrls.users.create.method,
      url: apiUrls.users.create.url,
      data: {
        ...date,
        emailVerified: true,
        phoneNumberVerified: true
      },
      headers: authHeaders
    };
    axios(options)
      .then(response => {
        console.log(response.data);
        dispatch(createUserSuccess(response.data, 'User Created Successfully'));
        dispatch(createUserClearMessageTimeout());
      })
      .catch(err => {
        const errors = {};
        if (err.response.statusText === 'Conflict') {
          const conflicts = err.response.data.conflicts;
          for (const error in conflicts) {
            errors[conflicts[error].key] = conflicts[error].message;
          }
        }
        dispatch(createUserFail(err.response.statusText, errors));
        dispatch(createUserClearMessageTimeout());
      });
  };
};
