import { apiUrls, axios, jsonStorage } from 'config';
import { SESSION_ACTION_TYPES } from './actionTypes';

const sessionLoginStart = () => {
  return {
    type: SESSION_ACTION_TYPES.LOGIN.START
  };
};

const sessionLoginSuccess = (session, user) => {
  return {
    type: SESSION_ACTION_TYPES.LOGIN.SUCCESS,
    redirectType: 'login',
    session: session,
    user: user
  };
};
const sessionLoginFail = error => {
  return {
    type: SESSION_ACTION_TYPES.LOGIN.FAIL,
    error: error
  };
};

const sessionLogoutSuccess = () => {
  return {
    type: SESSION_ACTION_TYPES.LOGOUT,
    redirectType: 'logout'
  };
};

const sessionLoginClearMessage = () => {
  return {
    type: SESSION_ACTION_TYPES.LOGIN.CLEAR_MESSAGE
  };
};

const sessionLoginClearMessageTimeout = () => {
  return dispatch => {
    setTimeout(() => {
      dispatch(sessionLoginClearMessage());
    }, 3000);
  };
};

export const sessionLogin = (email, password) => {
  return dispatch => {
    dispatch(sessionLoginStart());
    const options = {
      method: apiUrls.admin.login.method,
      url: apiUrls.admin.login.url,
      data: {
        email: email,
        password: password
      }
    };
    axios(options)
      .then(response => {
        const datetime = new Date(new Date().getTime() + 3600 * 1000);
        const data = response.data;
        if (data.roles.includes('Admin')) {
          const session = {
            expired: datetime,
            token: data.token,
            headers: {
              Authorization: 'Bearer '.concat(data.token)
            }
          };
          const user = {
            phoneNumber: data.phoneNumber,
            email: data.email,
            firstName: data.firstName,
            lastName: data.lastName,
            phoneNumberVerified: data.phoneNumberVerified,
            emailVerified: data.emailVerified
          };
          jsonStorage.setSession('session', 'true');
          jsonStorage.setItem('session', session);
          jsonStorage.setItem('user', user);
          dispatch(sessionLoginSuccess(session, user));
          dispatch(
            sessionTimeout((datetime.getTime() - new Date().getTime()) / 1000)
          );
          dispatch(sessionLoginClearMessageTimeout());
        } else {
          dispatch(sessionLoginFail('Unauthorised User'));
          dispatch(sessionLoginClearMessageTimeout());
        }
      })
      .catch(err => {
        if (err.response) {
          dispatch(sessionLoginFail(err.response.statusText));
        } else {
          dispatch(sessionLoginFail('Internal Server Error'));
        }
        dispatch(sessionLoginClearMessageTimeout());
      });
  };
};

export const sessionLogout = () => {
  return dispatch => {
    jsonStorage.clear();
    jsonStorage.removeSession('session');
    dispatch(sessionLogoutSuccess());
  };
};

const sessionTimeout = expired => {
  return dispatch => {
    setTimeout(() => {
      dispatch(sessionLogout());
    }, expired * 1000);
  };
};

export const sessionCheckState = () => {
  return dispatch => {
    const sessionCookie = jsonStorage.getSession('session');
    const session = jsonStorage.getItem('session');
    const user = jsonStorage.getItem('user');
    if (!sessionCookie) {
      dispatch(sessionLogout());
    } else {
      if (!session) {
        dispatch(sessionLogout());
      } else {
        const expired = new Date(session.expired);
        if (expired <= new Date()) {
          dispatch(sessionLogout());
        } else {
          dispatch(sessionLoginSuccess(session, user));
          dispatch(
            sessionTimeout((expired.getTime() - new Date().getTime()) / 1000)
          );
        }
      }
    }
  };
};
