export class jsonStorage {
  constructor () {
    this.storage = localStorage
  }

  static getItem (key) {
    const self = new this()
    const jsonObject = JSON.parse(self.storage.getItem(key))
    return (jsonObject || {})
  }

  static setItem (key, value) {
    const self = new this();
    const json = JSON.stringify(value)
    return self.storage.setItem(key, json)
  }

  static exist(key){
    const self = new this();
    return !!self.storage.getItem(key);
  }

  static removeItem (key) {
    const self = new this()
    self.storage.removeItem(key)
  }

  static clear () {
    const self = new this()
    self.storage.clear()
  }

  setCookie (cname, cvalue, cexpires, path) {
    document.cookie = `${cname}=${cvalue}; expires=${cexpires}; path=${path}`
  }

  getCookie (cname) {
    const name = cname + '='
    const decodedCookie = decodeURIComponent(document.cookie)
    const ca = decodedCookie.split(';')
    for (let i = 0; i < ca.length; i++) {
      let c = ca[i]
      while (c.charAt(0) === ' ') {
        c = c.substring(1)
      }
      if (c.indexOf(name) === 0) {
        return c.substring(name.length, c.length)
      }
    }
    return null
  }

  static setSession (key, value) {
    const self = new this()
    self.setCookie(key, value, '0', '/')
  }

  static getSession (key) {
    const self = new this()
    if (!self.getCookie(key)) {
      this.clear()
    }
    return self.getCookie(key)
  }

  static removeSession (key) {
    const self = new this()
    const expiryDate = new Date()
    expiryDate.setDate(expiryDate.getDate() - 1)
    const session = self.getCookie(key)
    if (session) {
      self.setCookie(key, session, expiryDate.toUTCString(), '/')
    }
  }
}
