import { updateObject } from 'utils/updateObject';

export const usersUpdateStart = (state, action) => {
  return updateObject(state, {
    update: {
      ...state.update,
      loading: true,
      user: null,
      error: false,
      errorMessage: null,
      success: false,
      successMessage: null
    }
  });
};

export const usersUpdateSuccess = (state, action) => {
  return updateObject(state, {
    update: {
      ...state.update,
      loading: false,
      user: action.user,
      error: false,
      errorMessage: null,
      success: true,
      successMessage: action.success
    }
  });
};

export const usersUpdateFail = (state, action) => {
  return updateObject(state, {
    update: {
      ...state.update,
      loading: false,
      user: null,
      error: true,
      errorMessage: action.error,
      success: false,
      successMessage: null
    }
  });
};

export const usersUpdateClearMessage = (state, action) => {
  return updateObject(state, {
    update: {
      loading: false,
      user: null,
      error: false,
      errorMessage: null,
      success: false,
      successMessage: null
    }
  });
};
