import React, { Fragment, Suspense } from 'react'
import { renderRoutes } from 'react-router-config'
import PropTypes from 'prop-types'
import { makeStyles } from '@material-ui/styles'
import { LinearProgress } from '@material-ui/core'
import { connect } from 'react-redux'
import { Redirect } from 'react-router'
import * as actions from '../../actions'

const useStyles = makeStyles(theme => ({
  content: {
    height: '100%',
    paddingTop: 56,
    [theme.breakpoints.up('sm')]: {
      paddingTop: 64
    }
  }
}))

const Auth = props => {
  const { route } = props
  const classes = useStyles()
  let sessionRedirect = null
  if (props.authentication.loggedIn) {
    sessionRedirect = <Redirect to={props.redirect.dashboardURL} />
  } else {
    if (!props.authentication.loggedOut) {
      props.sessionCheckState()
    }
  }

  return (
    <Fragment>
      {sessionRedirect}
      <main className={classes.content}>
        <Suspense fallback={<LinearProgress />}>
          {renderRoutes(route.routes)}
        </Suspense>
      </main>
    </Fragment>
  )
}

Auth.propTypes = {
  route: PropTypes.object
}

const mapStateToProps = state => {
  return {
    authentication: state.session.authentication,
    redirectType: state.session.redirectType,
    redirect: state.session.redirect
  }
}

const mapDispatchToProps = dispatch => {
  return {
    sessionCheckState: () => dispatch(actions.sessionCheckState())
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Auth)
