import React, { Fragment, useEffect } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import useRouter from 'utils/useRouter';
import * as actions from '../../actions';

const AuthGuard = props => {
  const { children } = props;

  const router = useRouter();

  useEffect(() => {
    if (!props.authentication.loggedIn && !props.authentication.loggedOut) {
      props.sessionCheckState();
    }
  }, []);

  if (props.redirectType === 'logout') {
    router.history.push(props.redirect.loginURL);
    return null;
  } else {
    return <Fragment>{children}</Fragment>;
  }
};

AuthGuard.propTypes = {
  children: PropTypes.node,
  roles: PropTypes.array.isRequired
};

AuthGuard.defaultProps = {
  roles: []
};

const mapStateToProps = state => {
  return {
    authentication: state.session.authentication,
    redirectType: state.session.redirectType,
    redirect: state.session.redirect
  };
};

const mapDispatchToProps = dispatch => {
  return {
    sessionCheckState: () => dispatch(actions.sessionCheckState())
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(AuthGuard);
