export function GroupedOrderedCostingList(listItems){
  let newlist = Array();

  const compareSort = (a, b, key)=>{
    const stringA = a[key].toUpperCase();
    const stringB = b[key].toUpperCase();
    let comparison = 0;
    if (stringA > stringB) {
      comparison = 1;
    } else if (stringA < stringB) {
      comparison = -1;
    }
    return comparison;
  }

  listItems.forEach((item)=>{
    let serviceIndex = newlist.findIndex((d)=>(
      d.service === item.service
    ))
    if(serviceIndex !== -1){
      let categoryIndex = newlist[serviceIndex].items.findIndex((d)=>( d.category === item.category ))
      if(categoryIndex !== -1){
        let subCategoryIndex = newlist[serviceIndex].items[categoryIndex].items.findIndex((d)=>( d.subCategory === item.subCategory ))

        if(subCategoryIndex !== -1){
          if(item.hasOwnProperty('minDistance') && item.hasOwnProperty('maxDistance')){
            let indexDistance = newlist[serviceIndex].items[categoryIndex].items[subCategoryIndex].items.findIndex((d)=>(
              d.minDistance === item.minDistance &&
              d.maxDistance === item.maxDistance
            ))
            if(indexDistance !== -1){
              newlist[serviceIndex].items[categoryIndex].items[subCategoryIndex].items[indexDistance].items.push(item)
              newlist[serviceIndex].items[categoryIndex].items[subCategoryIndex].items[indexDistance].items.sort((a, b)=>{return a.minQty - b.minQty})
            }else{
              newlist[serviceIndex].items[categoryIndex].items[subCategoryIndex].items.push({
                minDistance: item.minDistance,
                maxDistance: item.maxDistance,
                items: [item]
              })
            }
          }else{
            newlist[serviceIndex].items[categoryIndex].items[subCategoryIndex].items.push(item);
            newlist[serviceIndex].items[categoryIndex].items[subCategoryIndex].items.sort((a, b)=>{return a.minQty - b.minQty})
          }


        }else{
          let items = []
          if(item.hasOwnProperty('minDistance') && item.hasOwnProperty('maxDistance')){
            items.push({
              minDistance: item.minDistance,
              maxDistance: item.maxDistance,
              items: [item]
            })
          }else{
            items.push(item)
          }
          newlist[serviceIndex].items[categoryIndex].items.push({
            subCategory: item.subCategory,
            items: items
          })
        }

        newlist[serviceIndex].items[categoryIndex].items.sort((a, b)=> compareSort(a, b, 'subCategory'))
      }else{
        let items = []
        if(item.hasOwnProperty('minDistance') && item.hasOwnProperty('maxDistance')){
          items.push({
            minDistance: item.minDistance,
            maxDistance: item.maxDistance,
            items: [item]
          })
        }else{
          items.push(item)
        }
        newlist[serviceIndex].items.push({
          category: item.category,
          items: [{
            subCategory: item.subCategory,
            items: items
          }]
        })
      }

      newlist[serviceIndex].items.sort((a, b)=> compareSort(a, b, 'category'))

    }else{
      let items = []
      if(item.hasOwnProperty('minDistance') && item.hasOwnProperty('maxDistance')){
        items.push({
          minDistance: item.minDistance,
          maxDistance: item.maxDistance,
          items: [item]
        })
      }else{
        items.push(item)
      }
      newlist.push({
        service: item.service,
        items: [{
          category: item.category,
          items: [{
            subCategory: item.subCategory,
            items: items
          }]
        }]
      })
    }

    newlist.sort((a, b)=> compareSort(a, b, 'service'))
  })

  return newlist
}
