import { updateObject } from 'utils/updateObject';

export const adminAuthLoginStart = (state, action) => {
  return updateObject(state, {
    authentication: {
      ...state.authentication,
      error: false,
      success: false,
      errorMessage: null,
      successMessage: null,
      loading: true
    }
  });
};

export const adminAuthLoginSuccess = (state, action) => {
  return updateObject(state, {
    authentication: {
      ...state.authentication,
      loggedIn: true,
      error: false,
      errorMessage: null,
      loading: false,
      success: true,
      successMessage: 'Logged In Successfully',
      ...action.session
    },
    user: action.user,
    redirectType: 'login'
  });
};

export const adminAuthLoginFail = (state, action) => {
  return updateObject(state, {
    authentication: {
      ...state.authentication,
      error: true,
      success: false,
      successMessage: null,
      errorMessage: action.error,
      loading: false,
      loggedIn: false
    }
  });
};

export const adminAuthLoginClearMessage = (state, action) => {
  return updateObject(state, {
    authentication: {
      ...state.authentication,
      error: false,
      errorMessage: null,
      success: false,
      successMessage: null
    }
  });
};

export const adminAuthLogout = (state, action) => {
  return updateObject(state, {
    authentication: {
      ...state.authentication,
      loggedIn: false,
      loggedOut: true,
      error: false,
      errorMessage: null,
      success: false,
      successMessage: null,
      loading: false
    },
    user: null,
    redirectType: 'logout'
  });
};
